.broadcast-container {
    background-color: var(--surface-page);
    min-height: 100vh;
    display: flex;              /*   */
    justify-content: center;    /*   */
    align-items: center;        /*   */
    flex-direction: column;     /*   */
    padding: 20px;              /*   */
    text-align: center;
    color: var(--text-primary);
}

.broadcast-header {
    margin-bottom: 30px;
}

.broadcast-icon {
    font-size: 48px;
    color: var(--accent-primary);
}

.broadcast-header h2 {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 10px;
}

.broadcast-header p {
    color: var(--text-muted);
    font-size: 16px;
    line-height: 1.5;
}

.broadcast-card {
    margin: 0 auto;
    background-color: var(--surface-card);
    padding: 40px 30px;
    border-radius: 12px;
    max-width: 550px;
    box-shadow: 0 20px 46px rgba(24, 20, 56, 0.12); /*    */
    border: 1px solid var(--border-subtle);

}

.broadcast-card h3 {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
    text-align: left;
}

.broadcast-card p {
    font-size: 14px;
    color: var(--text-muted);
    margin-bottom: 10px;
    text-align: left;
}

.input-group {
    display: flex;
    gap: 8px;
}

.input-group input {
    flex: 1;
    padding: 10px 14px;
    border: 1px solid var(--border-subtle);
    border-radius: 8px;
    font-size: 14px;
    background-color: var(--surface-input);
    color: var(--text-primary);
}

.input-group button {
    background-color: var(--accent-primary);
    color: white;
    border: none;
    padding: 10px 16px;
    border-radius: 8px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 6px;
    font-weight: bold;
    transition: background-color 0.2s ease;
}

.input-group button:hover {
    background-color: var(--accent-hover);
}

.error-text {
    color: #d32f2f;
    font-size: 13px;
    margin-top: 6px;
}

.start-btn {
    width: 100%;
    color: white;
    border: none;
    padding: 10px 16px;
    border-radius: 8px;
    background-color: var(--accent-secondary);
    display: flex;
    justify-content: center;
    gap: 6px;
    font-weight: bold;
    text-decoration: none;
    font-size: 15px;
    transition: background-color 0.2s ease;
}

.start-btn:hover {
    background-color: var(--accent-hover);
}

.hr-with-text {
    display: flex;
    align-items: center;
    text-align: center;
    margin: 24px 0;
    color: var(--text-muted);
    font-size: 14px;
}

.hr-with-text::before,
.hr-with-text::after {
    content: "";
    flex: 1;
    border-top: 1px solid var(--border-subtle);
    margin: 0 12px;
}

.start-input-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.start-input-group label {
    display: block;
    font-weight: bold;
    margin-bottom: 6px;
    text-align: left;
    font-size: 14px;
    color: var(--text-primary);
}

.start-input-group input {
    flex: 1;
    padding: 10px 14px;
    border: 1px solid var(--border-subtle);
    border-radius: 8px;
    font-size: 14px;
    background-color: var(--surface-input);
    color: var(--text-primary);
}

.required {
    color: #e63946;
    margin-left: 4px;
}

.help-text {
    font-size: 12px;
    color: var(--text-muted);
    margin-top: 6px;
    text-align: left;
}

/*    */
.dark-mode .broadcast-container {
    background-color: var(--surface-page);
    color: var(--text-primary);
}

/*   */
.dark-mode .broadcast-icon {
    color: var(--accent-primary);
}

/*    */
.dark-mode .broadcast-header p {
    color: var(--text-muted);
}

/*    */
.dark-mode .broadcast-card {
    background-color: var(--surface-card);
    border-color: var(--border-subtle);
    box-shadow: 0 24px 48px rgba(24, 20, 56, 0.18);
}

/*     */
.dark-mode .broadcast-card p {
    color: var(--text-muted);
}

/*   */
.dark-mode .input-group input,
.dark-mode .start-input-group input {
    background-color: var(--surface-input);
    color: var(--text-primary);
    border: 1px solid var(--border-subtle);
}

/*    */
.dark-mode .input-group button {
    background-color: var(--accent-secondary);
    color: white;
}

.dark-mode .input-group button:hover {
    background-color: var(--accent-hover);
}

/*      */
.dark-mode .start-btn {
    background-color: var(--accent-secondary);
    color: white;
}

.dark-mode .start-btn:hover {
    background-color: var(--accent-hover);
}

/*   */
.dark-mode .hr-with-text {
    color: var(--text-muted);
}

.dark-mode .hr-with-text::before,
.dark-mode .hr-with-text::after {
    border-top: 1px solid var(--border-subtle);
}

/*    */
.dark-mode .start-input-group label {
    color: var(--text-primary);
}

/*    */
.dark-mode .help-text {
    color: var(--text-muted);
}

/*    */
.dark-mode .required {
    color: #ff6b6b;
}
